#header {
  background-image: linear-gradient(
    90deg,
    rgba(8, 142, 236, 1) 4%,
    rgba(9, 54, 121, 1) 50%,
    rgba(130, 205, 231, 1) 93%
  );
  border-top: 2px solid white;
  color: white;
  height: 15rem;
  text-align: center;
}

.header_text {
  position: relative;
  animation: textanmation 1.5s ease-in-out;
}
@keyframes textanmation {
  from {
    right: 1500px;
  }
  to {
    right: 0px;
  }
}



.header_container {
  display: flex;
  flex-direction: row;
  justify-content:center;
  align-items: center;
  position: relative;
  animation: hairanimation 3.5s ease-in-out;
}

@keyframes hairanimation {
  from {
    left: -1500px;
  }
  to {
    left: 0px;
  }
}


.aboutme_text {
  border: 2px solid white;
  border-radius: 3rem;
  width: 30%;
  margin-left: 2rem;
  padding: 1rem;
}

.image {
  width: 7rem;
  height: 7rem;
  border-radius: 5rem;
  border: 2px solid white;
}

.button {
    color: white;
    background-image: linear-gradient(
     90deg,
     rgba(7, 55, 224, 1) 20%,
     rgba(10, 131, 227, 1) 62%,
     rgba(10, 167, 227, 1) 74%
    );
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: .5rem;
    margin-left: 2rem;
    border: none;
  
}

.button:hover {
  background-image: linear-gradient(
    90deg,
    rgba(7, 55, 224, 1) 0%,
    rgba(10, 131, 227, 1) 42%,
    rgba(10, 167, 227, 1) 74%
  );
  height: 2.5rem;
  border-radius: 0.5rem;
  border-spacing: 0.5rem;
}